import {FoodItem} from './food-item';

export class TrayItem {
    public foodItem: FoodItem;
    public size = 'small';
    public quantity = 1;
    public options = {};
    public notes = '';

    get selectedOptions() {
        const resp = [];
        for (const id of Object.keys(this.options)) {
            resp.push({id: id, value: this.options[id]});
        }
        return resp;
    }

    get allOptions() {
        const resp = {};
        for (const option of this.foodItem.options) {
            for (const variation of option.variations) {
                resp[variation.id] = {variation: variation, option: option};
            }
        }
        return resp;
    }

    get price() {
        return this.quantity * this.unitPrice;
    }

    get unitPrice() {
        let price = +this.foodItem.price[this.size].price;
        const allOptions = this.allOptions;
        for (const option of this.selectedOptions) {
            price += +(allOptions[option.value].variation.price[this.size]);
        }
        return price;
    }
}



