import { NgModule } from '@angular/core';
import {RouterModule} from '@angular/router';
import {LandingScreenComponent} from './landing-screen/landing-screen.component';
import {VenueScreenComponent} from './venue-screen/venue.component';
import {ItemDetailsComponent} from './components/item-details/item-details.component';
import {EmptyComponent} from './components/empty/empty.component';
import {Constants} from './constants';
import {PlaceOrderComponent} from './components/place-order/place-order.component';

const routes = [
    { path: '', component: LandingScreenComponent, pathMatch: 'full' },
    { path: 'venue/:code', component: VenueScreenComponent,
        children: [
            { path: '', component: EmptyComponent, pathMatch: 'full', data: {mode: Constants.MENU_MODE} },
            { path: 'food-item-details/:id', component: ItemDetailsComponent, data: {mode: Constants.ITEM_DETAILS_MODE}},
            { path: 'tray-item-details', component: ItemDetailsComponent, data: {mode: Constants.ITEM_DETAILS_MODE}},
            { path: 'tray', component: EmptyComponent, data: {mode: Constants.TRAY_MODE}},
            { path: 'place-order', component: PlaceOrderComponent, data: {mode: Constants.PLACE_ORDER_MODE}},
        ]
    },
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],

    exports: [RouterModule]
})
export class AppRoutingModule { }



