import { Component, OnInit, OnDestroy } from '@angular/core';
import {TrayService} from '../../services/tray.service';
import {trigger, style, animate, transition} from '@angular/animations';
import {ActivatedRoute, Router} from '@angular/router';
import {TrayItem} from '../../models/tray-item';
import {Location} from '@angular/common';

@Component({
  selector: 'app-item-details',
  templateUrl: './item-details.component.html',
  styleUrls: ['./item-details.component.css'],
    animations: [
        trigger(
            'enterAnimation', [
                transition(':enter', [
                    style({transform: 'translateY(100%)'}),
                    animate('200ms', style({transform: 'translateY(0)'}))
                ]),
                transition(':leave', [
                    style({transform: 'translateY(0)', opacity: 1}),
                    animate('200ms', style({transform: 'translateY(100%)', opacity: 0}))
                ])
            ]
        )
    ]
})
export class ItemDetailsComponent implements OnInit, OnDestroy {
  trayItem: TrayItem;
  itemId: number;
  constructor(private trayService: TrayService,
              private route: Router,
              private activatedRoute: ActivatedRoute,
              private location: Location
  ) { }

  ngOnInit() {
    this.itemId = this.activatedRoute.snapshot.params['id'];
    this.trayItem = this.trayService.item || new TrayItem();
  }

  goBack() {
      if (this.isUpdateItem) {
          this.route.navigate(['../tray'], {relativeTo: this.activatedRoute});
      } else {
          this.route.navigate(['../../'], {relativeTo: this.activatedRoute});
      }
  }

  get item() {
      if (this.trayItem.foodItem) {
          return this.trayItem.foodItem;
      } else if (this.trayService.allItems[this.itemId]) {
          this.trayItem.foodItem = this.trayService.allItems[this.itemId];
      } else {
        return null;
      }
  }

  get isUpdateItem() {
      if (this.itemId) {
          return false;
      }
      return true;
  }

  add() {
      this.trayItem.foodItem = this.item;
      this.trayService.items.push(this.trayItem);
      this.goBack();
  }

  remove() {
      const index = this.trayService.items.indexOf(this.trayItem);
      this.trayService.items.splice(index, 1);
      this.goBack();
  }

  addQuantity(quantity: number) {
      if (this.trayItem.quantity + quantity > 0) {
          this.trayItem.quantity += quantity;
      }
  }

    ngOnDestroy(): void {
        this.trayService.item = null;
    }
}



