import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, NavigationEnd, NavigationExtras, Router} from '@angular/router';
import {VenueService} from '../venue.service';
import {Venue} from '../models/venue';
import {Category} from '../models/category';
import {trigger, style, animate, transition} from '@angular/animations';
import {FoodItem} from '../models/food-item';
import {TrayService} from '../services/tray.service';
import {TrayItem} from '../models/tray-item';
import 'rxjs/add/operator/filter';
import {Constants} from '../constants';
import {Location} from '@angular/common';

@Component({
  selector: 'app-venue-screen',
  templateUrl: './venue.component.html',
  styleUrls: ['./venue.component.css'],
  providers: [TrayService],
  animations: [
        trigger(
            'enterAnimation', [
                transition(':enter', [
                    style({height: '0', opacity: 0}),
                    animate('200ms', style({height: '150px', opacity: 1}))
                ]),
                transition(':leave', [
                    style({height: '100px', opacity: 1}),
                    animate('200ms', style({height: '0', opacity: 0}))
                ])
            ]
        )
    ]
})
export class VenueScreenComponent implements OnInit {
  venueCode: string;
  venue: Venue;
  venueLoading = false;
  venueLoaded = false;
  mode = Constants.MENU_MODE;
  Constants = Constants;

  constructor(public activatedRoute: ActivatedRoute,
              private venueService: VenueService,
              private route: Router,
              private location: Location,
              public trayService: TrayService) {}

  ngOnInit() {
    const self = this;
    this.venueCode = this.activatedRoute.snapshot.params['code'];
    this.venueLoading = true;
    this.venueService.getVenueByCode(this.venueCode).then((venue) => {
      self.venue = venue;
      self.venueLoading = false;
      self.venueLoaded = true;
      self.storeItems();
    });
      this.mode = this.activatedRoute.firstChild.snapshot.data.mode || Constants.MENU_MODE;
    this.route.events
        .filter(e => e instanceof NavigationEnd)
        .subscribe(e => {
          this.mode = this.activatedRoute.firstChild.snapshot.data.mode || Constants.MENU_MODE;
        });

  }

  expand(category: Category) {
    category.expanded = !category.expanded;
    for (const cat of this.venue.categories) {
      if (cat.expanded && cat.id !== category.id) {
        cat.expanded = false;
      }
    }
  }

    showDetailsFor(item: FoodItem) {
        this.route.navigate(['food-item-details/' + item.id], { relativeTo: this.activatedRoute});
    }

    trayItemDetails(item: TrayItem) {
      this.trayService.item = item;
        this.route.navigate(['tray-item-details/'], { relativeTo: this.activatedRoute});
    }
    showMenu() {
        if (this.mode === Constants.MENU_MODE) {
            this.toggleExpansion();
        } else {
            this.goBack();

        }
    }

    private goBack() {
        this.route.navigate(['./'], {relativeTo: this.activatedRoute});
    }

    private toggleExpansion() {
      let counter = 0;
      let expanded = false;
      for (const cat of this.venue.categories) {
          if (cat.expanded) { counter++; }
          if (counter > 1)  { expanded = true; break; }
      }
        for (const cat of this.venue.categories) {
                cat.expanded = expanded ? false : true;
        }
    }

    showtray() {
        this.route.navigate(['tray'], { relativeTo: this.activatedRoute});
    }

    placeOrder(){
        this.route.navigate(['place-order'], { relativeTo: this.activatedRoute});
    }

    storeItems() {
      for (const category of this.venue.categories) {
        for (const item of category.items) {
            this.trayService.allItems[item.id] = item;
        }
      }
    }
}



