import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'priceJson'
})
export class PriceJsonPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if (!value) {
      return[];
    }
    const res = [];
    for (const key of Object.keys(value)) {
      const price = JSON.parse(JSON.stringify(value[key]));
      price.key = key;
      price.name = price.name || key;
      res.push(price);
    }
    return res;
  }

}



