import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppComponent } from './app.component';
import { EnterVenueCodeComponent } from './enter-venue-code/enter-venue-code.component';
import { AppRoutingModule } from './app-routing.module';
import { LandingScreenComponent } from './landing-screen/landing-screen.component';
import {VenueService} from './venue.service';
import {HttpClientModule} from '@angular/common/http';
import {FormsModule} from '@angular/forms';
import { VenueScreenComponent } from './venue-screen/venue.component';
import { ImageComponent } from './components/image/image.component';
import { ItemDetailsComponent } from './components/item-details/item-details.component';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { EmptyComponent } from './components/empty/empty.component';
import { PriceJsonPipe } from './pipes/price-json.pipe';
import { ChipComponent } from './components/chip/chip.component';
import { TrayListItemComponent } from './components/tray-list-item/tray-list-item.component';
import { PlaceOrderComponent } from './components/place-order/place-order.component';


@NgModule({
  declarations: [
    AppComponent,
    EnterVenueCodeComponent,
    LandingScreenComponent,
    VenueScreenComponent,
    ImageComponent,
    ItemDetailsComponent,
    EmptyComponent,
    PriceJsonPipe,
    ChipComponent,
    TrayListItemComponent,
    PlaceOrderComponent,
  ],
  imports: [
      BrowserModule,
      AppRoutingModule,
      HttpClientModule,
      FormsModule,
      BrowserAnimationsModule
  ],
  providers: [
      VenueService,
      HttpClientModule,
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



