import {Component, ElementRef, HostBinding, HostListener, OnInit, AfterViewInit, ViewChild, Input} from '@angular/core';

@Component({
  selector: 'app-image',
  templateUrl: './image.component.html',
  styleUrls: ['./image.component.css']
})
export class ImageComponent implements OnInit, AfterViewInit {
  constructor(public el: ElementRef) {}
  @ViewChild('image') image: ElementRef;
  @Input('src') src = 'nothing';
  @Input('ratio') ratio = 0.698;
  height = 0;
  ngOnInit() {
  }

  ngAfterViewInit(): void {
    const self = this;
    setTimeout(() => {
        self.height = self.ratio * self.image.nativeElement.offsetWidth;
    }, 3);
  }

}



