import { Component, OnInit } from '@angular/core';
import {VenueService} from '../venue.service';
import {Venue} from '../models/venue';
import {Router} from '@angular/router';


@Component({
  selector: 'app-enter-venue-code',
  templateUrl: './enter-venue-code.component.html',
  styleUrls: ['./enter-venue-code.component.css']
})
export class EnterVenueCodeComponent implements OnInit {
  nearby: Array<Venue> = [];
  showNearbyVenue = false;
  loadingNearbyVenue = false;
  venueCode: string;
  constructor(public venueService: VenueService, public router: Router) {}

  ngOnInit() {}

  public locateVenueCode() {
    const self = this;
    this.showNearbyVenue = true;
    this.loadingNearbyVenue = true;
        navigator.geolocation.getCurrentPosition(function(position) {
            const location = {'latitude': position.coords.latitude, 'longitude': position.coords.longitude};
            self.venueService.getVenuesByLocation(location).then((resp) => {
              self.nearby = resp;
              self.loadingNearbyVenue = false;
            });
        });
    }

    submitForm() {
      console.log(this.venueCode);
      this.router.navigate(['/venue/' + this.venueCode]);
    }
}



