import { Injectable } from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Venue} from './models/venue';



@Injectable()
export class VenueService {
  url = 'https://oneorder.com.au/api';
  constructor(public http: HttpClient) { }
  public getVenuesByLocation(location): Promise<Venue[]> {
    return this.http.get<Venue[]>(this.url + '/nearby', {params : location}).toPromise();
  }

  getVenueByCode(venueCode: string): Promise<Venue> {
    return this.http.get<Venue>(this.url + '/venueDetails/' + venueCode, {params : {code: venueCode}}).toPromise();
  }
}



