import { Injectable } from '@angular/core';
import {TrayItem} from '../models/tray-item';

@Injectable()
export class TrayService {
  public item: TrayItem = null;
    allItems = {};
    items: Array<TrayItem> = [];
  constructor() { }

  get totalCost() {
    let total = 0;
    for (const item of this.items) {
          total += item.price;
    }
    return total;
  }

  get totalItems() {
      let total = 0;
      for (const item of this.items) {
          total += item.quantity;
      }
      return total;
  }

}



